<script>
  export default {
    name: 'Content',
    data() {
      return {
        Quote: "",
        Id : ""
      } 
    },
    methods: {
      async getQuote() {
        const res = await fetch('https://api.adviceslip.com/advice')
        const data = await res.json()
        console.log(data);


        this.Quote= data["slip"]["advice"]
        this.Id = data.slip.id
      }
    },
    created() {
      this.getQuote()
    },
  }
</script>

